<script>
export default {
    methods: {
        //  /    / 
        //         
        getData(loc) {
            const item = this.noteList[loc[0]]

            let type, name
            // 
            if (loc.length == 2) {
                type = "file"
                const folder = item[1]
                const folderName = item[0]
                const fileName = folder[loc[1]]
                name = `${folderName}/${fileName}`
            } else { // 
                if (typeof item == "string") {
                    // 
                    type = "file"
                    name = item
                } else if (typeof item == "object") {
                    // 
                    type = "folder"
                    name = item[0]
                }
            }
            return { type, name }
        }
    }
}
</script>