<script>
export default {
    data() {
        return {
            hover: false,
            setTimeout: null,
        }
    },
    emits: ["mouse-in-child"],
    methods: {
        //  
        onMouseEnter() {
            //  .4s 
            this.setTimeout = setTimeout(() => {
                this.hover = true
            }, 400)
            // 
            if (this.location.length > 1) {
                //  hover 
                this.$emit("mouse-in-child")
            }
        },
        // 
        onMouseLeave() {
            // 
            clearTimeout(this.setTimeout)
            if (!this.selected) {
                this.hover = false
            }
        }
    }
}
</script>